@import './Common.scss';

@include root_page('games-player') {
	main {
		border-radius: 10px;
		margin: calc(var(--nav-height-border) / 4) auto;
		background-color: var(--background-accent);
		display: flex;
		flex-direction: column;
		margin: 20px auto 0px auto;
		width: 95%;
		height: calc(100vh - var(--nav-height-border) - 40px);
		box-sizing: border-box;
		padding: 0px 15px;
		border-radius: 10px;

		> span {
			margin-top: 15px;
		}

		> .title {
			display: flex;
			flex-direction: row;
			align-items: center;
			height: 40px;
			line-height: 20px;
			align-items: center;
			margin: 10px 0px;

			> .name {
				margin: 0px;
				height: fit-content;
			}

			> .shift-right {
				margin-left: auto;
			}

			> .button {
				cursor: pointer;
				margin-left: 15px;
				border: 1px solid var(--border);
				background-color: var(--background);
				color: var(--text);
				border-radius: 5px;
				width: 40px;
				height: 40px;
				padding: 7px;
				box-sizing: border-box;

				&:hover {
					background-color: var(--background-accent);
					color: var(--text-accent);
				}
			}
		}

		> .frame {
			margin: 15px 0px;
			margin-bottom: 0px;
			width: 100%;
			height: 100%;

			> iframe {
				border: none;
				width: 100%;
				height: 100%;
			}

			> .controls {
				position: absolute;
				width: 300px;
				height: 350px;
				right: 0px;
				bottom: 0px;
				margin: 10px;
				border-radius: 5px;
				background-color: var(--background-accent);
				display: none;
				flex-direction: column;
				user-select: none;

				> .close {
					margin: 10px;
					margin-left: auto;
					width: 20px;
					height: 20px;
					padding: 5px;
					display: flex;
					align-items: center;
					justify-content: center;
					background-color: transparent;
					border: none;
					color: var(--text);
					border-radius: 5px;

					&:hover {
						background-color: var(--background);
					}
				}

				> .controls {
					display: flex;
					flex-direction: column;
					height: 100%;
					overflow-y: auto;

					> .control {
						display: flex;
						flex-direction: row;
						font-family: 'Montserrat Alternates';
						justify-content: space-around;
						align-items: center;
						margin: 3px 0px;

						> .visuals {
							width: 100%;
							font-size: 12px;
							display: flex;
							flex-direction: row;
							justify-content: center;

							> .move {
								display: flex;
								justify-content: center;
								align-items: center;
								flex-wrap: wrap;

								> .control-key:nth-child(1) {
									margin: 0 100%;
								}
							}

							.control-key {
								min-width: 25px;
								height: 25px;
								padding: 5px 7px;
								border-radius: 8px;
								border: 1px solid var(--text);
								color: var(--text);
								margin: 3px 2px;
								display: flex;
								justify-content: center;
								align-items: center;

								&.key-Space {
									width: 75px;
								}

								&.key-Enter {
									width: 75px;
								}
							}
						}

						.label {
							width: 100%;
							text-align: center;
							font-size: 14px;
						}
					}
				}
			}
		}

		&[data-controls='1'] {
			> .frame .controls {
				display: flex;
			}
		}

		&[data-panorama='1'] {
			position: fixed;
			top: 0px;
			left: 0px;
			z-index: 11;
			width: 100%;
			height: 100%;
			margin: 0px;
			padding: 0px;
			border-radius: 0px;

			> .title {
				width: calc(100% - 20px);

				> .name {
					display: none;
				}
			}

			> .frame {
				margin: 0px;
				width: 100%;
				height: 100%;
			}
		}
	}
}
